import { customElement, property } from 'lit/decorators.js';
import { unsafeCSS, CSSResultGroup, LitElement, TemplateResult } from 'lit';
import { classMap } from 'lit/directives/class-map.js';
import { styleMap } from 'lit/directives/style-map.js';
import { getColorVar } from '../../utils';
import cssStyles from './zwc-button_styles.scss?inline';
import { html, literal } from 'lit/static-html.js';

type ButtonType = 'button' | 'link';

type ButtonSize = 'lg' | 'md' | 'sm' | 'xs';

type ButtonStatus =
  | 'primary'
  | 'secondary'
  | 'tertiary-negative'
  | 'tertiary-positive'
  | 'tertiary-alert'
  | 'text';

@customElement('zwc-button')
export class ZwcButton extends LitElement {
  static override styles?: CSSResultGroup | undefined = unsafeCSS(cssStyles);

  @property()
  type: ButtonType = 'button';

  @property()
  size: ButtonSize = 'lg';

  @property()
  status: ButtonStatus = 'primary';

  @property({ type: String, attribute: 'text' })
  text = '';

  @property({ type: Boolean, attribute: 'inverted' })
  isInverted = false;

  @property({ type: Boolean, attribute: 'disabled' })
  isDisabled = false;

  @property({ type: Boolean, attribute: 'alt' }) // changes the icon position to the left (before text)
  isIconAlt = false;

  @property({ type: String })
  color = undefined;

  @property({ type: String })
  backgroundColor = undefined;

  render(): TemplateResult {
    const classes = {
      btn: true,
      [`btn--${this.status}`]: this.status,
      [`btn--${this.size}`]: this.size,
      inverted: this.isInverted,
      disabled: this.isDisabled,
      alt: this.isIconAlt,
    };

    const styles = {
      color: this.color ? getColorVar(this.color) : null,
      backgroundColor: this.backgroundColor
        ? getColorVar(this.backgroundColor)
        : null,
    };

    const tag = this.type === 'button' ? literal`button` : literal`a`;

    return html`
        <${tag} class="${classMap(classes)}" style="${styleMap(styles)}">
            ${this.text}
            <slot name="icon"></slot>
            <slot name="badge"></slot>
            <slot></slot>
        </${tag}>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-button': ZwcButton;
  }
}
